import  React from "react";
import { Link, withRouter } from "react-router-dom";
import {pemAuth} from "../../firebase/config";
import logo from "../../img/pem50.png"


function Navigation(props) {
     //Nav component with menu items
 

  return (
    <div className="navigation">
      <nav className="navbar navbar-expand navbar-dark bg-dark">
        <div className="container">
          <Link className="navbar-brand" to="/">
            <img src={logo} width='35px' height='30px' alt="PEM logo" />
          </Link>
          <div>
            <ul className="navbar-nav ml-auto">
              <li key="0"
                className={`nav-item  ${
                  props.location.pathname === "/" ? "active" : ""
                }`}>
                <Link className="nav-link" to="/">
                  Develop
                </Link>
              </li>
              <li key="1"
                className={`nav-item  ${
                  props.location.pathname === "/dces" ? "active" : ""
                }`}>
                <Link className="nav-link" to="/dces">
                 Run/Results
                </Link>
              </li>
              <li key="2" className="nav-item">
          <Link to="/" className="nav-link" onClick = {() => {pemAuth.signOut()}}>Sign out</Link>
        </li>
            </ul>
          </div>
        </div>
      </nav>
    </div>
  );
}

export default withRouter(Navigation);